import './solution.scss'

export default function Solution() {
    return (
        <div className="solution" id="solution">
            <div className="form">
                <div className="header">the old...</div>
                <div className="content"><p>You can see in the older web portal shown below that detail settings are much more spread out and there's much more information to navigate, causing a lot of time spent simply
                scrolling through the page. As shown in our proposed solution, it will allow users to save time completing tasks within the portal instead of wasting it in navigation by providing
                a UI that's much more cohesive and tight. This, along with many other quality of life features, will increase user productivity and ease anxiety.</p></div>
                <img src={require('../assets/image1.png').default} id='newimg' alt=''></img>
                <div className="header">...and the new</div>
                <div className="content"><p>Shown below is an example of what one of the pages in the new UI will look like. It's been designed in such a way that allows the user to quickly navigate the page by having
                                            options that are related to one-another delegated to specific tabs and buttons in order to decrease the amount of time spent navigating each page by having these options clustered
                                            together, instead of it being one extensively long page, which was an issue with the original employee portal.</p></div>
                <img src={require('../assets/image0.png').default} id='newimg' alt=''></img>
            </div>
        </div>
    )
}
