import './scope.scss'

export default function Scope() {
    return (
        <div className="scope" id="scope">
            <div className="form">
                <div className="header">what codename two addresses</div>
                <div className="content"><p>CodenameTwo specifically addresses the web portal which its employees interact with on a daily basis,
                                         its back-end handling of the database it hosts, as well as improving the front-end user
                                        navigation for an overall faster and more convenient experience for the user. On deployment, the database
                                        will be connected with the salesforce api and be utilized by RFgen representatives.</p></div>
                                        <br />
                <div className="header">the intended use for codename two</div>
                <div className="content"><p>RFgen employees will use the application to look up and to make changes to their internal database which contains data on their clients. The internal employees
                                            will also use the application to generate licenses that can be distributed to RFgen’s customers. While RFgen already has an existing web portal, the goal of our redesign
                                            is that the accessibility provided from our changes will significantly enhance the user's experience.</p></div><br /><br /><br />
                <img src={require('../assets/rfgen-logo.gif').default} alt=''></img>
            </div>
        </div>
    )
}
